.container {
  transform: rotateY(180deg);
  text-align: center;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 25px;
}

.tiles-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 25px;
  height: fit-content;
}

.content-wrapper {
  overflow: auto;
}

@media (max-width: 1100px) {
  .container {
    transform: rotateY(0deg);
    height: 90%;
  }
}