.container {
  transform: rotateY(180deg);
  text-align: center;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.heading {
  margin-bottom: 30px;
}

.info {
  margin-top: 60px;
}

@media (max-width: 1100px) {
  .container {
    transform: rotateY(0deg);
  }
}
