.container {
  margin: auto;
  width: 80%;
  height: fit-content;
  border: 3px solid var(--main-color);
  border-radius: 30px;
  position: relative;
  cursor: pointer;
  color: #fff;
}

@media (max-width: 1100px) {
  .container {
    width: 100%;
  }
}

.shade {
  background: linear-gradient(
    90deg,
    var(--shade-color) 0%,
    transparent 15%,
    transparent 85%,
    var(--shade-color) 100%
  );
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  border-radius: 30px;
}

.title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  z-index: 3;
  text-shadow: 0 0 25px #000;
}

.img {
  width: 100%;
  opacity: 0.5;
  border-radius: 30px;
}
